form {
  background-image: url('../../images/background-stars.jpeg') ;
  color: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  height: 100vh;
  background-attachment: fixed;
}

.star-wars-logo {
  width: 40vw;
  height: 52vh;
}

input {
  height: 1.5em;
  width: 20vw;
  margin: .5em;
  border-radius: 10px;
  font-size: 1.5em;
  transition: all .3s ease-in-out;
}

select {
  height: 1.5em;
  width: 20vw;
  margin: .5em;
  border-radius: 10px;
  font-size: 1.5em;
}

.enter {
  height: 2em;
  width: 20%;
  margin: .25em 1em 1em 1em;
  border-radius: 10px;
  font-size: 1.75em;
  background-color: #E8C539;
  font-weight: bold;
  transition: all 1s ease-in-out;
}

.enter:hover{
  cursor: pointer;
  background-color: rgb(206, 174, 45);
  box-shadow: 2px 2px 30px 15px rgba(255, 0, 0, 0.776);
  transition: all .3s ease-in-out;
}

.enter:active {
  transform: scale(.97);
}

.error-border {
  box-shadow: 3px 3px 21px 10px rgba(255, 0, 0, 0.776);
  transition: all .3s ease-in-out;
}
