@import url(https://fonts.googleapis.com/css?family=Open+Sans:300,400&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #09d3ac;
}

form {
  background-image: url(/SWAPI-trivia/static/media/background-stars.695ab299.jpeg);
  color: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  height: 100vh;
  background-attachment: fixed;
}

.star-wars-logo {
  width: 40vw;
  height: 52vh;
}

input {
  height: 1.5em;
  width: 20vw;
  margin: .5em;
  border-radius: 10px;
  font-size: 1.5em;
  -webkit-transition: all .3s ease-in-out;
  transition: all .3s ease-in-out;
}

select {
  height: 1.5em;
  width: 20vw;
  margin: .5em;
  border-radius: 10px;
  font-size: 1.5em;
}

.enter {
  height: 2em;
  width: 20%;
  margin: .25em 1em 1em 1em;
  border-radius: 10px;
  font-size: 1.75em;
  background-color: #E8C539;
  font-weight: bold;
  -webkit-transition: all 1s ease-in-out;
  transition: all 1s ease-in-out;
}

.enter:hover{
  cursor: pointer;
  background-color: rgb(206, 174, 45);
  box-shadow: 2px 2px 30px 15px rgba(255, 0, 0, 0.776);
  -webkit-transition: all .3s ease-in-out;
  transition: all .3s ease-in-out;
}

.enter:active {
  -webkit-transform: scale(.97);
          transform: scale(.97);
}

.error-border {
  box-shadow: 3px 3px 21px 10px rgba(255, 0, 0, 0.776);
  -webkit-transition: all .3s ease-in-out;
  transition: all .3s ease-in-out;
}


.movieContainer {
  background-image: url(/SWAPI-trivia/static/media/background-stars.695ab299.jpeg);
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  flex-wrap: wrap;
  background-attachment: fixed;
}

article {
  margin: 3% 1% 1% 1%;
  background-color: white;
  width: 320px;
  height: 590px;
  border: 10px solid white;
  position: relative;
  color: #000;
  -webkit-transition: all .3s ease-in-out;
  transition: all .3s ease-in-out;
}

article:hover {
  -webkit-transform: scale(1.03);
          transform: scale(1.03);
  box-shadow: 2px 2px 30px 15px rgba(255, 0, 0, 0.776);
}

h1 {
  position: absolute;
  color: white;
  top: 20px;
  left: 56px;
  font-size: 45px;
  font-family: 'Cormorant Garamond', serif;
  text-shadow: 2px 1.5px 1px black;
}

.image {
  background-image: url(/SWAPI-trivia/static/media/darth.4871db8e.jpg);
  background-size: cover;
  width: 100%;
  height: 472px;
  margin-bottom: 18px;
}

h3 {
  margin: 0px 0px 10px 0px;
}

.characters-button-container {
  width: 100%;
  height: 28px;
  display: flex;
  justify-content: center;
  text-align: center;
}

.characters {
  width: 100%;

  font-size: 1.25em;
  border-radius: 5px;
  background-color: #E8C539;
  margin: auto;
  font-weight: 600;
  text-decoration: none;
  color: #000;
}

.characters:hover {
  cursor: pointer;
  font-weight: 800;
}

.characters:active {
  -webkit-transform: scale(.97);
          transform: scale(.97);
}

span {
  font-weight: 400;
}



header {
  padding: .1%;
  background-color: #000;
  z-index: 2;
}

.header-container {
  background-color: #000;
  height: 125px;
  color: #fff;
  display: flex;
  justify-content: space-between;
  box-shadow: 0 30px 20px -20px red;

}

.header-quote {
  margin: auto 0px;
  font-size: 3em;
  color: #e8c539;
  font-family: 'Pathway Gothic One', sans-serif;
  font-style: italic;
}

.logout-button {
  width: 165px;
  height: 115px;
}

h2, p {
  margin: 0;
  font-family: 'Cormorant Garamond', serif;
}

.user-badge {
  height: 100px;
  text-align: center;
  margin: .5%;
  color: #e8c539;
  font-family: 'Pathway Gothic One', sans-serif;

}

.baby-yoda {
  width: 100px;
  margin: 0;
}

.character-container {
  background-image: url(/SWAPI-trivia/static/media/background-stars.695ab299.jpeg);
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  flex-wrap: wrap;
  background-attachment: fixed;
}

.movie-characters-banner {
  display: flex;
  height: 100px;
  width: 100vw;
  background-color: #000;
  box-shadow: 0 0px 34px 0px red;
  justify-content: center;
  align-items: center;
}

.movie-title-banner {
  font-size: 3em;
  color: #e8c539;
  font-family: 'Pathway Gothic One', sans-serif;
  font-style: italic;
}

.character-card {
  margin: 25px;
  background-color: white;
  width: 320px;
  height: 300px;
  color: #000;
  -webkit-transition: all .3s ease-in-out;
  transition: all .3s ease-in-out;
  padding-bottom: .5em;
}

.character-image {
  border-radius: 50px;
}

.character-name {
  display: inline-block;
  font-family: 'Cormorant Garamond', serif;
  color: #000;
  font-size: 1.75em;
  font-weight: bold;
  text-align: center;
}

article:hover {
  -webkit-transform: scale(1.03);
          transform: scale(1.03);
  box-shadow: 2px 2px 30px 15px rgba(255, 0, 0, 0.776);
}

.image-name-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1em;
}

.character-card-font {
  font-family: 'Arial', sans-serif;
  font-size: 1.25em;
}

.bold {
  font-weight: bold;
}

.fade {
  position: relative;
  width: 100%;
  top: 1px;
  background-image: -webkit-gradient(linear, left bottom, left top, from(transparent), color-stop(75%, black));
  background-image: linear-gradient(0deg, transparent, black 75%);
  z-index: 1;
  text-align: center;

}

.title {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.scrolling-text-container {
  display: flex;
  justify-content: center;
  position: relative;
  height: 50vh;
  overflow: hidden;
  font-size: 500%;
  font-weight: 600;
  letter-spacing: 6px;
  line-height: 150%;
  -webkit-perspective: 400px;
          perspective: 400px;
  text-align: center;
  color: #e8c539;
}

.scrolling-text-p-font {
  color: #e8c539;
  font-family: 'Pathway Gothic One', sans-serif;
}

.movie-title {
  color: #e8c539;
  font-family: 'Pathway Gothic One', sans-serif;
}

.scrolling-text {
  color: #e8c539;
  font-family: 'Pathway Gothic One', sans-serif;
}

.crawl {
  position: relative;
  top: 99999px;
  -webkit-transform-origin: 50% 100%;
          transform-origin: 50% 100%;
  -webkit-animation: crawl 150s linear;
          animation: crawl 150s linear;
  text-align: center;
}

.crawl > .title {
  font-size: 90%;
  text-align: center;
}

.crawl > .title h1 {
  margin: 0 0 100px;
  text-transform: uppercase;
}

@-webkit-keyframes crawl {
    0% {
    top: -100px;
    -webkit-transform: rotateX(20deg)  translateZ(0);
            transform: rotateX(20deg)  translateZ(0);
  }
    100% {
    top: -3500px;
    -webkit-transform: rotateX(25deg) translateZ(-2500px);
            transform: rotateX(25deg) translateZ(-2500px);
  }
}

@keyframes crawl {
    0% {
    top: -100px;
    -webkit-transform: rotateX(20deg)  translateZ(0);
            transform: rotateX(20deg)  translateZ(0);
  }
    100% {
    top: -3500px;
    -webkit-transform: rotateX(25deg) translateZ(-2500px);
            transform: rotateX(25deg) translateZ(-2500px);
  }
}

.loader {
  height: 100vh;
  width: 100%;
  background: -webkit-gradient(linear, left top, left bottom, from(rgba(0, 0, 0, 0.5)), to(rgba(0, 0, 0, 0.7))), url(/SWAPI-trivia/static/media/background-stars.695ab299.jpeg);
  background: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.7)), url(/SWAPI-trivia/static/media/background-stars.695ab299.jpeg);
  display: flex;
  justify-content: center;
  align-items: center;
}



.loading-title {
  color: #08d5d5;
  font-size: 4em;
  font-family: 'Open Sans', sans-serif;
  font-weight: 400;
}



/* loading dots */

.loading:after {
  content: ' .';
  -webkit-animation: dots 2s steps(5, end) infinite;
          animation: dots 2s steps(5, end) infinite;}

@-webkit-keyframes dots {
  0%, 20% {
    color: rgba(0,0,0,0);
    text-shadow:
      .25em 0 0 rgba(0,0,0,0),
      .5em 0 0 rgba(0,0,0,0);}
  40% {
    color: #08d5d5;
    text-shadow:
      .25em 0 0 rgba(0,0,0,0),
      .5em 0 0 rgba(0,0,0,0);}
  60% {
    text-shadow:
      .25em 0 0 #08d5d5,
      .5em 0 0 rgba(0,0,0,0);}
  80%, 100% {
    text-shadow:
      .25em 0 0 #08d5d5,
      .5em 0 0 #08d5d5;}}

@keyframes dots {
  0%, 20% {
    color: rgba(0,0,0,0);
    text-shadow:
      .25em 0 0 rgba(0,0,0,0),
      .5em 0 0 rgba(0,0,0,0);}
  40% {
    color: #08d5d5;
    text-shadow:
      .25em 0 0 rgba(0,0,0,0),
      .5em 0 0 rgba(0,0,0,0);}
  60% {
    text-shadow:
      .25em 0 0 #08d5d5,
      .5em 0 0 rgba(0,0,0,0);}
  80%, 100% {
    text-shadow:
      .25em 0 0 #08d5d5,
      .5em 0 0 #08d5d5;}}

